	module.exports = {"data":{"markdownRemark":{"html":"<p>大変お待たせいたしました。 <a href=\"/!/thC/\">東方夢終劇 ～ Concealed the Conclusion</a>の体験版A2を公開いたしました。予告どおりA2はA(例大祭版)のFIX版で、大きな拡張は特にありませんのでご了承ください。なお、今回特別にパッチ版の他に<em>フル版も完全公開</em>しますので例大祭行けなかった方や入手しそびれた方はこの機にぜひどうぞ。</p>\n<h2>6/11 4:10追記</h2>\n<p>パッチの当て方について少々説明不足だったので解凍メッセージの解説をもうちょっと詳しく書き直しました。</p>\n<h2>6/11 19:00追記</h2>\n<p>アクセス数ありえNEEEEEEEEEEEEE'Ａ｀ いつぞやの<a href=\"%22http://lama.danmaq.com/lama/\">幻想Lama祭</a>並だなこりゃ……っと言うわけでお待たせしました、拍手(もどき)レスですよー。</p>\n<h2>(拍)Web拍手レスまーだー</h2>\n<p>ごめんねお母さん忙しくってごめんね</p>\n<h2>(拍)すみません、ゲーム版よりも告知ページの魔理沙の方が明らかに可愛いんですが…</h2>\n<p>ごめんねお父さん忙しくってごめんね</p>\n<h2>(拍)夢終劇体験版ＷＥＢ公開楽しみにしてます。例大祭頒布はＣＤという有形状態での配布、高音質のＢＧＭというのが売りだと思うのでＷＥＢ版で音質落としての配布位なら特に問題ない（というか配布期待してる人が多い）と思いますよ。</h2>\n<p>ということで公開してみました。この機に是非。</p>\n<h2>(拍)すんごいオモロいです！けどルートＣが歯ごたえありすぎです！（笑</h2>\n<p>少々難しすぎるってことでA2では気持ちやわらかくしてみましたよ？</p>\n<h2>(拍)体験版ネット公開KITAAAAAAAAAAAA!!!1!</h2>\n<p>ｋｔｋｒ</p>\n<h2>(拍)今度時封城とレイラの原画集作ってください！</h2>\n<p>正気ですか！？</p>\n<h2>(拍)？＾＾？</h2>\n<p>？＿？</p>\n<h2>(拍)けーねの帽子がキムチ！</h2>\n<p>김치！</p>\n<h2>(拍)TEST</h2>\n<p>TEST Accept.</p>\n<h2>(拍)これ届いてるのかな…？</h2>\n<p>送信押しても確認も何も出ずに元のページ戻ってしまいますがちゃーんと送れてますよ。もし不安でしたら何も描かずに送信してみてくださいな、エラーで弾かれれば正常です。</p>","frontmatter":{"cover":null,"date":"2006-06-11T00:00:00+09:00","strDate":"2006/6/10","title":"夢終劇 体験版A2"}}},"pathContext":{"path":"/ja/blog/2006/06/11-夢終劇-体験版a2/","langKey":"ja"}}

